






























export default {
	data() {
		return {
			info:'',
			dc:'',
			dc_id:'',
			status:0
		};
	},
	onLoad(option) {
		var that = this;
		uni.showLoading({
			title:'数据加载中...'
		})
		uni.request({
			url:that.http+'index/getDcList',
			header:that.header,
			method:'POST',
			data:{
				net_id:option.id
			},
			success: (res) => {
				console.log(JSON.stringify(res.data))
				uni.hideLoading()
				uni.setStorageSync('wdId',res.data.net_info.id)
				// that.$store.state.wdId = res.data.net_info.id
				that.info = res.data.net_info;
				that.dc = res.data.data;
				that.status = res.data.data[0].id
			},
			fail() {
				uni.showToast({
					title:'网络延迟，请刷新后重试!',
					icon:'none'
				})
			}
		})
	},
	methods:{
		// 选择电池id
		changeDc(e,id){
			this.status = id
		},
		// 打电话
		call(){
			uni.makePhoneCall({
				phoneNumber:this.info.link_tel.toString()
			})
		},
		// 点击预约安装
		next(){
			var that = this;
			// 先判断是否实名
			// 先判断是否实名
			uni.request({
				url:that.http+'index/ifIdcard',
				method:'POST',
				header:that.header,
				data:{
					user_id:uni.getStorageSync('token')
				},
				success: (res) => {
					console.log(that.status)
					if(res.data.data==1){
						uni.setStorageSync('dcId',that.status)
						uni.navigateTo({
							url:'../yuYue2/yuYue2?id='+that.status
						})
					}else{
						console.log(2)
						uni.navigateTo({
							url:'../realName/realName'
						})
					}
				},
				fail() {
					uni.showToast({
						title:'网络延迟，请刷新后重试!',
						icon:'none'
					})
				}
			})
		}
	}
}
